import React, {Component} from 'react';
import Cards from "./Cards";
import {Droppable} from "react-beautiful-dnd";

class Cycle extends Component {
    constructor(props) {
        super(props);
        this.state = {name: this.props.name, cards: this.props.cards};
    }

    render() {
        return (
            <div className="w-full sm:w-full md:w-1/2 lg:1/4 xl:w-1/4">
                <div className={"rounded text-gray-700 cardpannel m-2 shadow-inner border-t-4 border-"+this.props.color+"-400"}>
                    <div className="pt-2 pl-4 h-12 primarycolor">
                        <p className="text-xl float-left font-sans font-bold">
                            {this.state.name}
                        </p>
                        <span className="float-right pt-1 pr-3 ">
                                <i className="fa fa-plus"></i>
                            </span>
                    </div>
                    <Droppable droppableId={this.props.id + ""}>
                        {(provided) => (
                            <div ref={provided.innerRef}
                                 {...provided.droppableProps}>
                                <div className="min-h-full min-w-full p-2">
                                    {this.state.cards.map((card, index) => <Cards card={card} key={card.id} index={index}/>
                                    )}
                                </div>
                                {provided.placeholder}
                            </div>
                        )
                        }
                    </Droppable>
                </div>
            </div>
        );
    }
}

export default Cycle;
