import React, {Component} from 'react';
import Cfd from "./Cfd";
import Cts from "./Cts";
import Cth from "./Cth";

class Charts extends Component {
    render() {
        return (<div className="flex flex-wrap w-full my-1">
                <div className="h-auto xl:w-1/3 md:w-1/2 w-full my-1">
                    <div className="bg-white shadow-md rounded m-2 h-full justify-center content-center flex-wrap flex">
                        <div className="text-center w-full p-2">
                            <p className="p-1 font-sans text-gray-700 font-medium text-sm">
                                Cumulative Flow Diagram
                            </p>
                            <Cfd/>
                        </div>

                    </div>
                </div>
                <div className="h-auto xl:w-1/3 md:w-1/2 w-full my-1">
                    <div className="bg-white shadow-md rounded m-2 h-full justify-center content-center flex-wrap flex">
                        <div className="text-center w-full p-2">
                            <p className="font-sans text-gray-700 font-medium text-sm">
                                Cycle Time Scatterplot
                            </p>
                            <Cts/>
                        </div>
                    </div>
                </div>
                <div className="h-auto xl:w-1/3 md:w-1/2 w-full my-1">
                    <div className="bg-white shadow-md rounded m-2 h-full justify-center content-center flex-wrap flex">
                        <div className="text-center w-full p-2">
                            <p className="font-sans text-gray-700 font-medium text-sm">
                                Cycle Time Histogram
                            </p>
                            <Cth/>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default Charts;
