import React, {Component} from 'react';
import {Link} from 'react-router-dom';

class Navlist extends Component {
    constructor(props) {
        super(props);
        this.wrapper=React.createRef()
        this.name = this.props.name;
        if (props.child == null) {
            this.childlist = null;
            this.dropicon = null;
            this.link =
                <Link to={this.props.link} onClick={this.props.toggleSidebar}>
                    <div className="p-3 h-12 navs z-20" >
                        <h3 className="float-left pl-3"><i className={this.props.icon+" px-2"} aria-hidden="true"></i>{this.name}</h3>
                        <span className="float-right">{this.dropicon}</span>
                    </div>
                </Link>;
        } else {
            this.dropicon = <i className="fa fa-chevron-down" style={{cursor:'pointer'}}></i>;
            this.childlist = this.props.child.map(child => {
                return <Link to={child.link} key={child.id} onClick={this.props.toggleSidebar}>
                    <li className="h-10">
                        <p className="pl-8 p-2">{child.name}</p>
                    </li>
                </Link>
            });
            this.link =
                <div className="p-3 h-12 navs z-20" onClick={this.toggleList}>
                    <h3 className="float-left pl-3"><i className={this.props.icon+" px-2"} aria-hidden="true"></i>{this.name}</h3>
                    <span className="float-right">{this.dropicon}</span>
                </div>;

        }
    }

    toggleList = () => {
        const wrap=this.wrapper.current;
        wrap.classList.toggle('collapsed');
    }

    render() {
        return (
            <div className="text-gray-200 ">
                {this.link}
                <div className="collapsible-wrapper collapsed" ref={this.wrapper}>
                <ul className="navlist collapsible w-full" >
                    {this.childlist}
                </ul>
                </div>
            </div>
        );
    }
}

export default Navlist;
