import React, {Component} from 'react';

class CardsWork extends Component {
    constructor(props) {
        super(props);
        switch (this.props.card.priority) {
            case "High":
                this.color = "bg-pink-500";
                break;
            case "Medium":
                this.color = "bg-blue-500";
                break;
            case "Low":
                this.color = "bg-green-500";
                break;
            default:
        }
    }
    render() {
        return (
            <li className="border-l-2 py-1 border-gray-500 w-full">
                <div className="bg-white w-auto m-2 pl-5 rounded hoverbg">
                    <div className="flex">
                        <div className="p-2">
                            <div className="flex">
                                <div className="pl-1">
                                    <p className={this.color + " text-gray-100 text-xs shadow rounded-full px-4"}>
                                        {this.props.card.priority}
                                    </p>
                                </div>
                            </div>
                            <div className="text-blue-800 text-left lg:text-lg md:text-base">
                                <p className="font-medium py-2">
                                    {this.props.card.name}
                                </p>
                                <div className="flex pr-4">
                                    {this.props.card.tags.map((tag,index) => {
                                        return <div className="pl-1" key={index}>
                                            <p
                                                className="bg-gray-300 text-blue-800 font-medium text-xs shadow-sm rounded-full px-2">
                                                {tag.name}
                                            </p>
                                        </div>
                                    })}
                                </div>
                                <div className="flex">
                                    <div className="p-1 float-left">
                                        <p className="text-blue-700 font-medium text-sm">
                                            <i className="fa fa-user pr-1" aria-hidden="true"></i>
                                            {this.props.card.peoples} Peoples
                                        </p>
                                    </div>
                                    <div className="p-1 float-right">
                                        <p className="text-blue-700 font-medium text-sm">
                                            <i className="fa fa-comments pr-1" aria-hidden="true"></i>
                                            {this.props.card.comments} Comments
                                        </p>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="pt-8 p-2 hidden sm:block">
                            <img alt="userprofile" className="flex h-10 rounded-full shadow-xs shadow-md" src="/tushar.webp"/>
                        </div>
                        <div className="pt-8 p-2">
                            <div
                                className="flex h-10 rounded-full text-blue-700 shadow-md border-2 border-blue-300 hover:bg-blue-600 duration-300 hover:text-gray-200"
                                style={{cursor: 'pointer'}}
                            >
                                <i className="fa fa-plus m-3" aria-hidden="true"></i>
                            </div>
                        </div>
                    </div>
                </div>
            </li>
        );
    }
}

export default CardsWork;
