import React, {Component} from 'react';

class EpicModalForm extends Component {
    render() {
        return (
            <div className="w-full " ref={this.props.wrapper}>
                <div className="fixed h-full w-full bg-gray-900 bg-opacity-75 z-10 flex justify-center">
                    <div className="bg-white lg:w-1/2 w-full self-center m-4 lg:m-0">
                        <div className="flex-1">
                            <div className="float-right text-blue-600 text-2xl p-2">
                                <i className="fa fa-times cursor-pointer" onClick={this.props.togglemodal}
                                   aria-hidden="true"></i>
                            </div>
                            <div className="w-full text-gray-700 text-lg font-medium p-2 text-center">
                                {this.props.epic.name}
                            </div>
                            <div className="w-full">
                                <form className="bg-white rounded px-8 pt-6 pb-8 mb-4">
                                    <div className="mb-4">
                                        <label className="block text-gray-600 text-sm font-semibold mb-2"
                                               htmlFor="title">
                                            Name
                                        </label>
                                        <input
                                            className="bg-gray-100 appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline"
                                            id="title"
                                            type="text"
                                            placeholder={this.props.epic.name}
                                        />
                                    </div>
                                    <div className="mb-4">
                                        <label
                                            className="block text-gray-600 text-sm font-semibold mb-2"
                                            htmlFor="username"
                                        >
                                            Description
                                        </label>
                                        <textarea
                                            rows="4"
                                            cols="50"
                                            className="bg-gray-100 p-1 appearance-none border rounded w-full text-gray-700 leading-tight focus:outline-none focus:shadow-outline"
                                            id="description"
                                            type="text"
                                        />
                                    </div>
                                    <div className="mb-4 flex">
                                        <label
                                            className="p-2 text-gray-600 text-sm font-semibold mb-2"
                                        >
                                            Assignee:
                                        </label>
                                        <div className="p-2 hidden sm:block">
                                            <img alt="userprofile" className="flex h-10 rounded-full shadow-xs shadow-md" src="/tushar.webp"/>
                                        </div>
                                        <div className="p-2">
                                            <div
                                                className="flex h-10 rounded-full text-blue-700 shadow-md border-2 border-blue-300 hover:bg-blue-600 duration-300 hover:text-gray-200"
                                                style={{cursor: 'pointer'}}
                                            >
                                                <i className="fa fa-plus m-3" aria-hidden="true"></i>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="flex items-center justify-between">
                                        <div
                                            className="bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded focus:outline-none focus:shadow-outline cursor-pointer"
                                        >
                                            Save
                                        </div>
                                        <div
                                            className="inline-block align-baseline font-bold text-sm text-blue-500 hover:text-blue-800 cursor-pointer"
                                            onClick={this.props.togglemodal}
                                        >
                                            cancel
                                        </div>
                                    </div>
                                </form>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default EpicModalForm;
