import React, {Component} from 'react';
import Cycle from "./Cycle";
import {DragDropContext} from "react-beautiful-dnd";


class Kanban extends Component {
    state = {
        cycle: [
            {
                id: 0, name: "To Do",
                color:"green",
                cards: [
                    {
                        id: 0,
                        name: "Prepare Structural Documentation",
                        priority: "Medium",
                        tags: [{name: "Developement"}, {name: "Archetecture"}],
                        peoples: 2,
                        comments: 5
                    },
                    {
                        id: 1,
                        name: "Prepare Structural Documentation",
                        priority: "High",
                        tags: [{name: "Developement"}, {name: "Archetecture"}],
                        peoples: 2,
                        comments: 5
                    },
                    {
                        id: 2,
                        name: "Prepare Structural Documentation",
                        priority: "Medium",
                        tags: [{name: "Developement"}, {name: "Archetecture"}],
                        peoples: 2,
                        comments: 5
                    },
                    {
                        id: 3,
                        name: "Prepare Structural Documentation",
                        priority: "Low",
                        tags: [{name: "Developement"}, {name: "Archetecture"}],
                        peoples: 2,
                        comments: 5
                    }
                ]
            },
            {
                id: 1, name: "In Progress",
                color:"blue",
                cards: [
                    {
                        id: 4,
                        name: "Prepare Structural Documentation",
                        priority: "High",
                        tags: [{name: "Developement"}, {name: "Archetecture"}],
                        peoples: 2,
                        comments: 5
                    },
                    {
                        id: 5,
                        name: "Prepare Structural Documentation",
                        priority: "Medium",
                        tags: [{name: "Developement"}, {name: "Archetecture"}],
                        peoples: 2,
                        comments: 5
                    },
                    {
                        id: 6,
                        name: "Prepare Structural Documentation",
                        priority: "Low",
                        tags: [{name: "Developement"}, {name: "Archetecture"}],
                        peoples: 2,
                        comments: 5
                    }
                ]
            },
            {
                id: 2, name: "Code Review",
                color: "blue",
                cards: [
                    {
                        id: 7,
                        name: "Prepare Structural Documentation",
                        priority: "High",
                        tags: [{name: "Developement"}, {name: "Archetecture"}],
                        peoples: 2,
                        comments: 5
                    },
                    {
                        id: 8,
                        name: "Prepare Structural Documentation",
                        priority: "Medium",
                        tags: [{name: "Developement"}, {name: "Archetecture"}],
                        peoples: 2,
                        comments: 5
                    },
                    {
                        id: 9,
                        name: "Prepare Structural Documentation",
                        priority: "Low",
                        tags: [{name: "Developement"}, {name: "Archetecture"}],
                        peoples: 2,
                        comments: 5
                    }
                ]
            },
            {
                id: 3, name: "Done",
                color: "red",
                cards: [
                    {
                        id: 10,
                        name: "Prepare Structural Documentation",
                        priority: "High",
                        tags: [{name: "Developement"}, {name: "Archetecture"}],
                        peoples: 2,
                        comments: 5
                    },
                    {
                        id: 11,
                        name: "Prepare Structural Documentation",
                        priority: "Medium",
                        tags: [{name: "Developement"}, {name: "Archetecture"}],
                        peoples: 2,
                        comments: 5
                    },
                    {
                        id: 12,
                        name: "Prepare Structural Documentation",
                        priority: "Low",
                        tags: [{name: "Developement"}, {name: "Archetecture"}],
                        peoples: 2,
                        comments: 5
                    }
                ]
            },
        ],
    };

    onDragEnd = result => {
        if(result!=null && result.destination!=null){
            this.state.cycle[parseInt(result.destination.droppableId)].cards.splice(result.destination.index,0,
                this.state.cycle[parseInt(result.source.droppableId)].cards.splice(result.source.index,1)[0]);
            this.setState({});
        }
    }

    render() {
        return (
            <DragDropContext onDragEnd={this.onDragEnd}>
                <div className="flex flex-wrap">
                    {this.state.cycle.map((cycle,index) => <Cycle name={cycle.name} key={index} cards={cycle.cards}
                                                          id={index} color={cycle.color}/>)}
                </div>
            </DragDropContext>
        );
    }


}

export default Kanban;
