import React, {Component} from 'react';
import Logo from "./Logo";
import Navmenu from "./Navmenu";
import ProfileInfo from "./ProfileInfo";
import TopNav from "./TopNav";

class Sidebar extends Component {
    constructor(props) {
        super(props);
        this.wrapper=React.createRef();
    }

    toggleSidebar = () => {
        const wrap=this.wrapper.current;
        wrap.classList.toggle('hidesidebar');
    }

    render() {
        return (
            <div>
                <TopNav toggleSidebar={this.toggleSidebar}/>
                <div ref={this.wrapper} className="lg:block sidebar hidesidebar">
                    <div className="h-full lg:w-56 font-sans font-medium fixed z-10 w-full bgsidebar">
                        <Logo/>
                        <div className="h-auto mt-10 text-base">
                            <Navmenu toggleSidebar={this.toggleSidebar}/>
                        </div>
                        <ProfileInfo/>
                    </div>
                </div>
            </div>
        );
    }
}

export default Sidebar;
