import React, {Component} from 'react';
import Navlist from "./Navlist";

class Navmenu extends Component {
    NavList = [
        {
            id: 0,
            name: "Home",
            link: "/",
            icon: "fa fa-home",
            child: null
        },
        {
            id: 1,
            name: "Kanban Board",
            link: "/board",
            icon: "fa fa-calendar-check-o",
            child: null
        },
        {
            id: 2,
            name: "Roadmap",
            link: "/gannt",
            icon: "fa fa-bar-chart",
            child: null
        },
        {
            id: 3,
            name: "Menu",
            child: [
                {
                    id: 0,
                    name: "Menu 1",
                    link: "/menu1"
                },
                {
                    id: 1,
                    name: "Menu 2",
                    link: "/menu2"
                },
            ]
        },
        {
            id: 4,
            name: "Menu",
            child: [
                {
                    id: 0,
                    name: "Menu 1",
                    link: "/menu1"
                },
                {
                    id: 1,
                    name: "Menu 2",
                    link: "/menu2"
                },
            ]
        },
    ];

    render() {
        return (
            <div>
                {this.NavList.map(nav => {
                    return (<div key={nav.id}>
                        <Navlist name={nav.name} child={nav.child} link={nav.link} toggleSidebar={this.props.toggleSidebar}
                                 icon={nav.icon}
                        /></div>);
                })}
            </div>
        );
    }
}

export default Navmenu;
